.accordion-list {
  display: inline-flex;
  max-height: 140px;
  flex-direction: column;
  flex-wrap: wrap;
  list-style: none;
  column-gap: 50px;
  padding: 0;
  margin: 0;
  color: rgba(255, 255, 255, 0.7);
  column-count: 2;

  @include vp-1439 {
    max-height: max-content;
    flex-wrap: nowrap;
  }

  @include vp-1023 {
    max-height: max-content;
    flex-wrap: nowrap;
  }

  @include vp-767 {
    max-height: max-content;
    flex-wrap: nowrap;
    padding-left: 5px;
  }

  & li {
    page-break-inside: avoid;
    break-inside: avoid;
    font-weight: 400;
    font-size: 14px;
    line-height: 19px;

    &:first-of-type {
      grid-column-start: 1;
    }

    &:first-of-type + li {
      grid-column-start: 1;
    }

    &:nth-of-type(4n) {
      grid-column-start: 1;
    }

    &:nth-of-type(5n) {
      grid-column-start: 2;
      grid-row-start: 1;

      @include vp-1023 {
        grid-column-start: 1;
        grid-row-start: auto;
      }
    }

    &:nth-of-type(6n) {
      grid-row-start: 2;
      grid-column-start: 2;

      @include vp-1023 {
        grid-column-start: 1;
        grid-row-start: auto;
      }
    }

    &:last-of-type {
      grid-row-start: 3;
      grid-column-start: 2;

      @include vp-1023 {
        grid-column-start: 1;
        grid-row-start: auto;
      }
    }

    @include vp-767 {
      &:first-of-type br {
        display: none;
      }
    }
  }

  .accordion-list__item--third {
    grid-column-start: 1;
  }

  & li:not(:first-of-type),
  & li:not(:last-of-type) {
    margin-bottom: 10px;

    @include vp-767 {
      margin-bottom: 7px;
    }
  }

  & li:nth-child(4n) {
    margin-bottom: 0;
  }

  & p {
    color: rgba(255, 255, 255, 0.7);
  }

  & a {
    white-space: nowrap;
    display: block;
    font-family: $ff-ptmono;
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    color: rgba(255, 255, 255, 0.7);

    @include hover-focus {
      background-color: #cccccc;
      color: $color-font-black;
    }

    @include vp-767 {
      font-size: 14px;
      line-height: 25px;
    }
  }
}
